import service from "../../service.js";
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components:{
	uniLoadMore,
	},
	data() {
		return {
			dataList:[],
			title:'',
			detail:'',
			str:false,
			informList: [],
			heroList: [],
			wtiing: "", //已产出车贝
			wti: "", // 用户车贝
			consss: "",
			activity: "", // 马力
			glory: "", // 荣耀值
			integral: "", // 积分
			dailyCheckinweb: '', //签到成功要跳转的网页
			swipers: [{
				src: '../../static/index/banner.png'
			}],
			navs_img: [{
				id: 1,
				src: "../../static/index/home.png",
				name: "当家做主"
			}],
			floor_cont_list: [{
				num: '1.02',
				name: "车贝",
				id: 1
			}],
			loadshow: true,
			colorshow: false,
			isLoading: false,
			scroll: false,
			status: 0,
			pages: 1,
			limit: 15,
			page: 0,
			out: 0,
		}
	},
	
	methods: {
		Loadthepage() {
			
			var self = this;
			if (!self.loadshow) {
				return;
			}
			if (self.page <= self.pages) {
				var page = self.page + 1;
				console.log(this);
				this.$service.auth(
					this,
					this.$service.api.index.cargetArticle,
					'GET',
					{	
						// id:
						page: page,
						limit: 15,
					},
					function(self, res) {
						self.pages = res.data.last_page;
						self.page = page;
						// self.activity = res.data.activity
						var list = res.data.data;
						self.usdt = list.usdt;
						self.alipay = list.alipay;
						list.forEach((v, i) => {
							self.dataList.push(v);
						});
						self.$forceUpdate();
						console.log(self.dataList);
						if (self.pages == self.page || self.dataList.length == 0) {
							self.loadshow = false;
							uni.showToast({
								title: '数据已加载完毕',
								icon: 'none'
							});
							console.log('1', self.page, self.pages);
						} else {
							self.loadshow = true;
							console.log('2', self.page, self.pages);
						}
					}
				);
			} else {
				self.loadshow = false;
				uni.showToast({
					title: '数据已加载完毕',
					icon: 'none'
				});
				console.log('3', self.page, self.pages);
			}
		},
		//点击跳转到更多汽车资讯
		more(url){
			uni.navigateTo({
				url: url
			});
		},
		// 点击跳转到汽车资讯的文章
		carDetails(url){
			uni.navigateTo({
				url: url
			});
		},
		good(){
			this.str = false
		},
		xxxxx() {
			uni.showToast({
				title: "功能开发中",
				icon: 'none'
			})
		},
		jump(url) {
			uni.navigateTo({
				url: url
			});
		},
		jum(url, e) {
			if (e == 1) {
				var wasCarServ = {
					type: e,
					name: "洗车服务"
				};
				uni.setStorageSync('wasCarServ', {
					type: e,
					name: "洗车服务"
				});
			}
			uni.switchTab({
				url
			});
		},
		tiaoz(e) {
			uni.setStorageSync('webUrl', e);
			uni.navigateTo({
				url: '/pages/index/webview'
			});
		},
		scanCode() {
			uni.scanCode({
				success: function(res) {

					var arr = res.result.split('-');
					uni.setStorageSync('item', arr)
					uni.navigateTo({
						url: '/pages/main/scan'
					})
					/* console.log('条码内容：' + res.result); */
				}
			});
		},
		// 我要加油
		myGasoline(){ 
			var url ='https://hxtx.96zb.com/gash5/gaslist.html?parentId=0277a9657124478da48ce6095715ab41&mobile=' + uni.getStorageSync('member').account
			uni.setStorageSync('webUrl', url);
			uni.navigateTo({
				url: '/pages/index/webview'
			});
		},
		jumpJd(){
			var self = this;
			uni.request({
				url: service.api.record.jdList,
				method:"GET",
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('token')
				},
				success: (res) => {
					var res = res.data;
					if (res.code == 1) {
						uni.showToast({
							title: res.info,
							icon: 'none'
						});
						var dailyCheckinweb = res.data;
						console.log(dailyCheckinweb)
						if (dailyCheckinweb == undefined || dailyCheckinweb == '') {
			
							console.log("no")
						} else {
							uni.setStorageSync('webUrl', dailyCheckinweb);
							// setTimeout(() => {
								uni.navigateTo({
									url: "/pages/index/webview"
								});
								// window.location.href = dailyCheckinweb;
							// }, 1500)
						}
					} else {
						uni.showToast({
							title: res.info,
							icon: 'none'
						});
					}
					// console.log(res);
				}
			})
		},
		dailyCheckin() {
			var self = this;
			uni.request({
				url: service.api.main.dailyCheckin,
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('token')
				},
				success: (res) => {
					var res = res.data;
					if (res.code == 1) {
						uni.showToast({
							title: res.info,
							icon: 'none'
						});
						var dailyCheckinweb = res.data;
						console.log(dailyCheckinweb)
						if (dailyCheckinweb == undefined || dailyCheckinweb == '') {

							console.log("no")
						} else {
							uni.setStorageSync('webUrl', dailyCheckinweb);
							setTimeout(() => {
								uni.navigateTo({
									url: "/pages/index/webview"
								});
								// window.location.href = dailyCheckinweb;
							}, 1500)
						}
						setTimeout(() => {
						service.auth(self, service.api.main.asset, 'GET', '', function(self, res) {
							// console.log(res)
							var member = res.data.list;
							uni.setStorageSync("memberss", member);
							self.wti = member.wti;
							self.activity = member.activity;
							self.glory = member.glory;
							self.integral = member.integral;
							self.wtiing = res.data.wti;
						});
							}, 1500)

					} else {
						uni.showToast({
							title: res.info,
							icon: 'none'
						});
					}
					// console.log(res);
				}
			})
		},
		lookDeail(e) {
			var e = this.informList;
			var crateTime = e.createtime;
			var time = crateTime.substr(0, 11); //字符串方法
			var times = time.split('-'); //字符串方法
			var time = times.join('/'); //数组方法
			uni.setStorageSync('noticeTime', time);
			uni.setStorageSync('noticeConten', e.content);
			uni.navigateTo({
				url: '/pages/index/noticeDeail'
			});
			// console.log(e, "infoxxxxxxxxxx")
		},


		/*----获取位置权限----*/

		/* uni.getLocation({//H5无法获取经纬
		    type: 'gcj02',
		    success:(res)=>{
		        console.log('当前位置的经度：' + res.longitude ,"longitude");
		        console.log('当前位置的纬度：' + res.latitude);
		    }
